.resume-container {
    text-align: center;
    margin: 20px;
   
  }
  
  .resume-container h2 {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .resume-container p {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .download-btn {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    background-color: #007bff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .download-btn:hover {
    background-color: #0056b3;
  }
  